@import url(https://fonts.googleapis.com/css?family=Gayathri|Lato|Roboto&display=swap);
body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
}
.header-color {
  background: #4ecdc4; /* fallback for old browsers */
  background: -webkit-linear-gradient(
    to left,
    #556270,
    #4ecdc4
  ); /* Chrome 10-25, Safari 5.1-6 */
  background: -webkit-gradient(
    linear,
    right top, left top,
    from(#556270),
    to(#4ecdc4)
  );
  background: -webkit-linear-gradient(
    right,
    #556270,
    #4ecdc4
  );
  background: linear-gradient(
    to left,
    #556270,
    #4ecdc4
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.landing-grid {
  background: -webkit-linear-gradient(135deg, rgb(98, 82, 238), #556270, #23D5AB);
  background: linear-gradient(-45deg, rgb(98, 82, 238), #556270, #23D5AB);
  background-size: 600% 400%;
  text-align: center;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  -webkit-animation: change 30s ease-in-out infinite;
          animation: change 30s ease-in-out infinite;
}

@-webkit-keyframes change {
  0% {
    background-position: 0 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0 50%;
  }
}

@keyframes change {
  0% {
    background-position: 0 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0 50%;
  }
}

.projects-grid {
  display: flex;
}

/* Resume Page  */

.resume-right-col {
  background: #27221f;
  color: white;
  padding: 2em;
}

/* Contact Page */

.contact-body {
  margin: auto;
  position: fixed;
  width: 100%;
  height: 100%;
  justify-content: center;
  padding-top: 2em;
}

.contact-grid {
  text-align: center;
  justify-content: center;
  width: 80%;
  height: 550px;
  background: white;
  color: black;
  margin-bottom: 20px;
}

.contact-grid h3 {
  font-family: "Oxygen", sans-serif;
}

.contact-grid p {
  font-family: "Quicksand", sans-serif;
  font-weight: bold;
  margin-top: 50px;
}

.contact-grid hr {
  border-top: 3px solid black;
  width: 50%;
  margin: auto;
}

.contact-grid i {
  font-size: 20px;
  padding-right: 5rem;
}

.avatar-img {
  height: 250px;
  padding-top: 5em;
  border-bottom-left-radius: 25px;
}

.banner-text {
  background-color: black;
  opacity: 0.8;
  width: 75%;
  margin: auto;
  border-radius: 10px;
}

.banner-text h1 {
  font-size: 50px;
  font-weight: bold;
  color: white;
}

.banner-text hr {
  border-top: 3px solid white;
  width: 50%;
  margin: auto;
}

.banner-text p {
  color: white;
  font-size: 20px;
  padding: 1em;
}

.social-links {
  display: flex;
  justify-content: space-between;
  width: 50%;
  margin: auto;
}

.social-links i {
  color: white;
  font-size: 5em;
  padding: 8px;
}

body {
  background: #deefe7;
  font-family: "Lato", sans-serif;
}

.projects-grid {
  display: grid;
  width: 100%;
  margin-left: 0%;
  grid-template-columns: 300px 300px 300px;
  align-items: center;
  justify-content: space-evenly;
}

.projects-grid :first-child {
  align-self: center;
}
.projects-grid a {
  background-color: red;
  color: white;
  padding: 1em 1.5em;
  text-decoration: none;
  text-transform: uppercase;
  margin-left: 15px;
}

.projects-grid a:hover {
  background-color: #555;
}

.projects-grida:active {
  background-color: black;
}


.project-express {
  display: grid;
  align-items: center;
  justify-content: space-evenly;
  width: 100%;
  grid-template-columns: 300px 300px 300px;
}

.project-express a {
  background-color: red;
  color: white;
  padding: 1em 1.5em;
  text-decoration: none;
  text-transform: uppercase;
  margin: 0 auto;
}

.project-express a:hover {
  background-color: #555;
}

.project-express a:active {
  background-color: black;
}

